import _ from 'lodash';
import { LOSER_LEVEL, LOSER_LEVEL_LIQUIDITY } from '../config/appConfig';
import tokenHelper from '../helper/tokenHelper';
import { DApp, ITokenDetail } from '../model/tokenModel';
import { IBrowserNotification } from '../model/notificationModel';

export function mappingVariationBrowserNotification(
  tokens: ITokenDetail[]
): IBrowserNotification[] {
  let notifications: IBrowserNotification[] = [];

  tokens.forEach((token: ITokenDetail) => {
    if (token.variationNotification?.isPush) {
      const notification: IBrowserNotification = {
        address: token.address,
        title: buildTitleNotification(token),
        icon: token.logoURI,
        body: buildBodyNotification(token),
      };
      const liquidity: number = _.round(token.targetPool?.liquidity as number, 2);
      const loser: number = Math.abs(
        _.round(token.variationPrice as number, 2)
      );

      if (liquidity < LOSER_LEVEL_LIQUIDITY.A.LIQUIDITY) {
        loser >= LOSER_LEVEL_LIQUIDITY.A.LOSER &&
          notifications.push(notification);
      } else if (liquidity < LOSER_LEVEL_LIQUIDITY.B.LIQUIDITY) {
        loser >= LOSER_LEVEL_LIQUIDITY.B.LOSER &&
          notifications.push(notification);
      } else if (liquidity < LOSER_LEVEL_LIQUIDITY.C.LIQUIDITY) {
        loser >= LOSER_LEVEL_LIQUIDITY.C.LOSER &&
          notifications.push(notification);
      } else {
        notifications.push(notification);
      }
    }
  });

  notifications = _.chain(notifications).reverse().uniqBy('address').value();

  return notifications;
}

function buildTitleNotification(token: ITokenDetail): string {
  const icon: string = getIconLevelLoser(
    Math.abs(_.round(token.variationPrice as number, 2))
  );

  return `${icon} ${
    _.isNil(token.burn)
      ? token.symbol || token.name
      : '*[' +
        tokenHelper.buildBurnPercent(token.burn) +
        '] ' +
        (token.symbol || token.name)
  } ${_.round(token.variationPrice as number, 2)}%`;
}

function getIconLevelLoser(loser: number): string {
  if (loser > LOSER_LEVEL.C) {
    return 'ðŸ‡';
  }
  if (loser > LOSER_LEVEL.B) {
    return 'ðŸŠ';
  }
  if (loser > LOSER_LEVEL.A) {
    return 'ðŸ';
  }

  return 'ðŸ’¹';
}

function buildBodyNotification(token: ITokenDetail): string {
  return `ðŸ’µ $${tokenHelper.roundCurrency(
    token.price as number
  )}\nðŸ‘‰ ${tokenHelper.convertToInternationalCurrencySystem(
    token.targetPool?.liquidity as number
  )} (${
    token.targetPool?.dApp === DApp.PANCAKE
      ? ''
      : token.targetPool?.dApp + ' | '
  }${
    token.targetPool?.quoteSymbol
  })  -${tokenHelper.convertToInternationalCurrencySystem(
    Math.abs(token.variationLiquidity as number)
  )}ðŸ’°`;
}

export function mappingVariationBrowserNotificationFomo(
  tokens: ITokenDetail[]
): IBrowserNotification[] {
  let notifications: IBrowserNotification[] = [];

  tokens.forEach((token: ITokenDetail) => {
    if (token.variationNotificationFomo?.isPush) {
      const notification: IBrowserNotification = {
        address: token.address,
        title: `${
          _.isNil(token.burn)
            ? token.symbol || token.name
            : '*[' +
              tokenHelper.buildBurnPercent(token.burn) +
              '] ' +
              (token.symbol || token.name)
        } +${_.round(token.variationPrice as number, 2)}%`,
        icon: token.logoURI,
        body: `$${tokenHelper.roundCurrency(
          token.price as number
        )} | ${tokenHelper.convertToInternationalCurrencySystem(
          token.targetPool?.liquidity as number
        )} (${token.targetPool?.dApp} | ${token.targetPool?.quoteSymbol})`,
      };

      notifications.push(notification);
    }
  });

  return notifications;
}
